
.Information{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100%;
    z-index: 0;

    background: #aa4b6b;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #3b8d99, #6b6b83, #aa4b6b);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #3b8d99, #6b6b83, #aa4b6b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.bg{
    z-index: 0;    
    width: 100vw;
    position: fixed;
    top: 0px;
    left: 0px;
    mask-image: linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,1));
}
.info__cover{
    position: fixed;
    top : 20vh;
    left : 5vw;
    z-index: 70;
    width: auto;
    height: 75vh;

    border-radius: 20px;
    box-shadow: 10px 10px 20px rgba(0,0,0,0.5);

}

.infoBox{
    position : relative;
    display: flex;
    flex-direction: column;
    z-index: 30;

    padding-top : 0px;
    padding-right: 40px;
    padding-left: 50vw;
    box-sizing: border-box;
    
    border-top: 20px solid  rgba(0,0,0,0);
    border-bottom: 20px solid  rgba(0,0,0,0);

    margin-top: 30vh;

    width : 100vw;
    height: 60vh;

    color: white;
    background-color: rgba(0,0,0,0.5);
    box-shadow: 10px 10px 20px rgba(0,0,0,0.5);
    z-index: 50;
    overflow-y: scroll;
    scroll-margin-top: 300px;
}

.infoBox>div:first-child{
    font-size: 30px;
    font-weight: 800;
    text-align: center;
    margin-bottom: 30px;
    
    color:palevioletred;
}
.infoBox>div:nth-child(2){
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: 600;
}
.infoBox__ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0px;
}
.infoBox__ul li{
    margin: 0px 20px;
}
.infoBox__descrition{

    font-size: 18px;
    text-indent: 20px;
    line-height: 1.5em;
}
.infoLodingPic{
    position: fixed;
    top : 45vh;
    font-size: 60px;
}
@media screen and (orientation: portrait) {
	.info__cover{
        top : 22vh;
        left: 35vw;

        width: 30vw;
        height: 40vw;
    }
    .infoBox{
        border-top: 30vw solid  rgba(0,0,0,0);
        border-bottom: 10px solid  rgba(0,0,0,0);
        padding : 30px;
        padding-top: 10px;
    }
}
.hidden{
    width: 0px;
}